@use "../utils/vars" as *;
@use "../utils/extends" as *;
@use "../utils/mixins" as *;

.form {
  display: flex;
  flex-direction: column;
  background-color: $black-background;
  color: $white-background;
  border: none;

  &__field {
    width: max-content;
    color: $white-background;
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 600;
    font-size: 15px;
    line-height: 22px;
    padding-bottom: 16px;
    margin-bottom: 40px;
    border-bottom: 1px solid $white-background;

    &:nth-child(6) {
      margin-bottom: 80px;
    }

    &:hover {
      border-bottom: 2px solid $button-blue;
    }

    &:hover::placeholder {
      color: $white-background;
    }

  }
}
